import React, { useState, useEffect, Fragment } from "react";
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux'
import configureStore from './store';
import './index.css';
import App from './App';

import config from "./utils/config";
import Spinner from "./components/Spinner/index";

import { accountsSdk } from "@livechat/accounts-sdk";
import LiveChat from "@livechat/agent-app-widget-sdk";
import "@livechat/design-system/dist/design-system.css";

const useLiveChat = ({ client_id, account_url }) => {
    const [accessToken, setAccessToken] = useState(null);
    LiveChat.init({ authorize: false });

    useEffect(() => {
        accountsSdk.init({
            client_id,
            onIdentityFetched: (error, data) => {
                if (data && data.access_token) {
                    console.log('++++++++++++++++WORKS!!!');
                    setAccessToken(data.access_token);
                } else {
                    window.location.href = `${account_url}?response_type=token&client_id=${client_id}&redirect_uri=${
                        window.location.href
                    }`;
                }
            }
        });
    });

    return [accessToken];
};

const AppWithAuth = () => {
    const [accessToken] = useLiveChat(config);

    if (!accessToken) {
        return <Spinner marginTop="calc(100% - 50px)" />;
    }

    return (
        <Fragment>
            <App accessToken={accessToken} />
        </Fragment>
    );
};

ReactDOM.render(<Provider store={configureStore()}>
    <AppWithAuth />
</Provider>, document.getElementById('root'));

